.app {
    padding: 20px;
  }
  
  .header {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 20px;
    position: relative;
  }
  
  .cart-icon {
    font-size: 24px;
    cursor: pointer;
  }
  
  .cart-count {
    background-color: red;
    color: white;
    border-radius: 50%;
    padding: 2px 8px;
    font-size: 14px;
    position: absolute;
    top: -10px;
    right: -10px;
  }
  
  .products {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    padding: 50px;
   }
   
  .product, .cart-item {
    border: 3px solid #ccc;
    padding: 10px;
    width: 200px;
    text-align: center;
   
  }
  
  .product img, .cart-item img {
    width: 100px;
    height: 100px;
    object-fit: cover;
  }
  
  .cart {
    border: 1px solid #ccc;
    padding: 20px;
    width: 300px;
    position: fixed;
    top: 50px;
    right: 20px;
    background: white;
    background-color: rgb(236, 230, 230);
    text-align: center;
  }
 
  .cart h4 {
    margin-top: 0;
  }
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #d16a6a;
    box-shadow: 0 2px 4px #797474;
    border: 0;
    font-weight: bold;
    color: rgb(255, 250, 250);
  }
  .nav ul {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
 
  }
  .nav li {
    margin-left: 20px;
  }
  .nav a {
    color: rgb(227, 225, 225);
    text-decoration: none;
    font-size: 18px;
  }
   .nav a:hover {
    text-decoration: underline;
  }
  .header-right {
    display: flex;
    align-items: center;
  }
  
  .search-bar {
    display: flex;
    align-items: center;
    background-color: white;
    padding: 5px;
    border-radius: 20px;
  }
  
  .search-bar input {
    border: none;
    outline: none;
    padding: 5px;
  }
  
  .search-bar button {
    background: none;
    border: none;
    color: #333;
    cursor: pointer;
 }
 